ul{
    margin-top:0;
    margin-bottom:0;
}

.toolbar{
    position:fixed;
    height:60px;
    width:100%;
    top:0;
    left:0;
    background-color:#1ae396;
    z-index: 500;
}

.toolbar_navigation{
    display:flex;
    height:100%;
    align-items:center;
    padding:0 1rem;
}

.toolbar_logo{
    margin-left:0.5rem;
}

.toolbar_logo a{
    color:white;
    text-decoration: none;
    font-size:1.5rem;
}

.spacer{
    flex:1 1;
}

.toolbar_navigation_items ul{
    list-style: none;
    margin:0;
    padding:0;
    display:flex;
}

.toolbar_navigation_items li {
    padding: 0 0.5rem;
}

.toolbar_navigation_items a {
    color:white;
    text-decoration: none;
}

.toolbar_navigation_items a:hover,
.toolbar_navigation_items a:active {
    color:#06422b;
}

@media (max-width:768px){
    .toolbar_navigation_items{
