.new_cases{
    background:white;
  font-family: 'Montserrat', sans-serif;
  font-size:14px;
  width:100%;
  margin-top:80px;
  text-align:center;
  border-radius:8px;
  -webkit-box-shadow: 0px 0px 23px 8px rgba(234,234,234,1);
-moz-box-shadow: 0px 0px 23px 8px rgba(234,234,234,1);
box-shadow: 0px 0px 23px 8px rgba(234,234,234,1);
cursor: pointer;
transition: transform .2s
}
.new_cases:hover{
    transform: scale(1.1);
}
.new_cases .new_cases_title{
padding:8px;
border-top-left-radius: 8px;
border-top-right-radius: 8px;
}
.new_cases h4{
    margin:0;
    font-size:17px;
}
.new_cases p{
    text-align: center;
    font-size: 13px;
    font-style: italic;
    padding-bottom:10px;
    padding-right:10px;
    margin:0;
}
.new_cases .new_cases_title h1{
    color:white;
    font-size:22px;
    font-weight: 500;
    margin:0;
}
.new_cases .center{
    display: block;
  margin-left: auto;
  margin-right: auto;
}
.new_cases .center img{
    height:16px;
    width:16px;
}
#days{
    text-align: right;
}
#figures{
    text-align: left;
    padding-right: 10px;
}
.row_class{
    padding:5px 15px;
}
.green{
    color:green;
    font-weight: bold;
}
.red{
    color:red;
    font-weight: bold;
}
.spacing{
    padding:15px 0px;
}